p, h1, h2, h3, h4, h5, h6, span {
  font-family: Arial, Helvetica, sans-serif;
  font-size: larger;
}

.postCard {
  width: fit-content;
  min-width: 200px;
  max-width: 700px;
  background-color: #b8b8b8;
  border-radius: 20px;
  gap: 16px;
  margin: 16px;
  padding: 20px;
  display: block;
}

.posts {
  justify-content: flex-start;
  display: flex;
}

.like {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background: rgba(91, 91, 91, .608);
  border: 0;
  border-radius: 5px;
  padding: 6px;
  font-size: 16px;
  transition: transform 1s;
}

.like:hover {
  background-color: #4b4b4b;
  transform: rotate(1800deg);
}

